.about {
    margin: 30px 0px;
    padding: 20px;
    margin-left: 200px;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}

.about-img img {
    border-radius: 10px;
    width: 100%;
    height: 400px;
}

.about-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.about-content h1 {
    color: gray;
    font-family: cursive;
    border-bottom: 2px solid gray;
    text-align: center;
}

.about-content p {
    text-align: justify;
    padding: 20px;
}

/* .about .card {
    cursor: pointer;
} */

.about .tech-icon {
    font-size: 2rem;
    margin-right: 10px;
}

.about .card:hover {
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
        rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
        rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    /* background-color: #138781;
    color: white; */

    transition: 1s;
}


/* ======== DARK MODE ========= */

#dark .about {
    box-shadow: none;
    background-color: rgba(0, 0, 0, 0.15);
}

#dark .about-content h1 {
    color: #b0a4a4;
    border-bottom: 2px solid #b0a4a4;
}

/* ================================ */
@media (max-width: 600px) {
    .about {
        margin: 0;
        padding: 0;
        margin-top: 20px;
        width: 100%;
    }

    .about-content h1 {
        margin-top: 10px;
    }
}